.smart-header__container {
  display: flex;
  background: $color-black-pearl;
  min-height: 120px;
  position: relative;

  @media (max-width: $vp-1024) {
    min-height: 100px;
  }

  @media (max-width: $vp-768) {
    min-height: 70px;
  }
}

.smart-header {
  background-color: $color-whisper;
  top: -90px;
  left: 0;
  width: 100%;
  z-index: 10;
  transition: all .2s ease;

  @media (max-width: $vp-1024) {
    transition: none;
    top: 0;
  }
}

.smart-header.fixed {
  position: fixed;
  transform: translateY(90px);

  @media (max-width: $vp-1024) {
    position: static;
    transform: none;
  }
}

.smart-header__container::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}

.smart-header__logo-image {
  @media (max-width: $vp-768) {
    width: 35px;
    height: 35px;
  }
}

.smart-header__wrapper-logo {
  z-index: 2;
  padding-top: 30px;
  padding-right: 160px;
  padding-left: 310px;
  position: relative;

  @media (max-width: $vp-1680) {
    padding-left: 260px;
  }

  @media (max-width: $vp-1600) {
    padding-left: 200px;
    padding-right: 100px;
  }

  @media (max-width: $vp-1440) {
    padding-left: 150px;
    padding-right: 80px;
  }

  @media (max-width: $vp-1280) {
    padding-left: 70px;
    padding-right: 30px;
  }

  @media (max-width: $vp-1024) {
    padding-top: 20px;
    padding-left: 26px;
  }

  @media (max-width: $vp-768) {
    padding-left: 9px;
    padding-top: 15px;
  }
}
