.sedona-video__title {
  margin: 0;
  padding-top: 17px;
  font-size: 18px;
  text-align: center;
  text-transform: uppercase;
  padding-bottom: 27px;

  @media (min-width: $tablet-width) {
    padding-top: 58px;
    font-size: 30px;
    padding-bottom: 30px;
  }
}

.sedona-video__container {
  background-color: #ffffff;

  @media (min-width: $tablet-width) {
    padding-bottom: 40px;
  }

  @media (min-width: $sedona-desktop-width) {
    padding-bottom: 82px;
  }
}

.sedona-video__confirmation {
  margin: auto;
  border: none;
  height: 177px;
  width: 280px;

  @media (min-width: $tablet-width) {
    width: 648px;
    height: 500px;
  }

  @media (min-width: $sedona-desktop-width) {
    width: 800px;
  }
}

.sedona-video__button {
  cursor: pointer;
  position: absolute;
  top: 63px;
  left: 124px;
  border: none;
  background-color: rgba(0, 0, 0, 0);

  @media (min-width: $tablet-width) {
    top: 223px;
    left: 297px;
  }

  @media (min-width: $sedona-desktop-width) {
    top: 223px;
    left: 371px;
  }
}

.sedona-video__wrapper {
  position: relative;
  background-image: url("../img/video-mobile.jpg");
  width: 280px;
  height: 177px;
  margin: auto;
  background-color: #000000;
  background-repeat: no-repeat;

  @media (min-width: $tablet-width) {
    background-image: url("../img/video-tablet.jpg");
    width: 648px;
    height: 500px;
    margin: auto;
  }

  @media (min-width: $sedona-desktop-width) {
    background-image: url("../img/video-desktop.jpg");
    width: 800px;
  }
}

.sedona-video__description {
  margin: auto;
  padding-bottom: 38px;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
  color: rgba(51, 51, 51, 1);
  width: 266px;

  @media (min-width: $tablet-width) {
    width: 445px;
    padding-bottom: 92px;
  }
}
